const axios = require('axios')

const TagsApi = {
   	baseUrl: 'https://nameless-lowlands-64942.herokuapp.com/tags',

    listTags: function(){
        return axios.get(this.baseUrl)
        	.then (res=> res.data.data)
    },
}

module.exports = TagsApi

