const axios = require('axios')

const BreedsApi = {
    baseUrl: 'https://nameless-lowlands-64942.herokuapp.com/breed',

    listBreeds: function(idUser){
        return axios.get(this.baseUrl)
        	.then (res=> res.data.data)
    },
}

module.exports = BreedsApi


